"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { createClient } from "@/lib/supabase/client"
import { ArrowLeft, Plus, Edit2, Trash2, Search, Tag, FolderOpen } from "lucide-react"

interface MenuCategory {
  id: string
  name: string
  parent_id: string | null
  restaurant_id: string
  display_order?: number
  parent?: MenuCategory
}

interface MenuItem {
  id: string
  name: string
  category: string
  category_id?: string | null
  price: number
  description?: string
  available: boolean
  is_available?: boolean
  stock?: number
  image_url?: string
  restaurant_id: string
  menu_category?: MenuCategory
}

export default function MenuPage() {
  const router = useRouter()
  const supabase = createClient()
  const [loading, setLoading] = useState(true)
  const [menuItems, setMenuItems] = useState<MenuItem[]>([])
  const [filteredItems, setFilteredItems] = useState<MenuItem[]>([])
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedParentCategory, setSelectedParentCategory] = useState<string>('all')
  const [selectedSubCategory, setSelectedSubCategory] = useState<string>('all')
  const [showModal, setShowModal] = useState(false)
  const [showCategoryModal, setShowCategoryModal] = useState(false)
  const [editingItem, setEditingItem] = useState<MenuItem | null>(null)
  const [restaurant, setRestaurant] = useState<any>(null)
  
  const [allCategories, setAllCategories] = useState<MenuCategory[]>([])
  const [parentCategories, setParentCategories] = useState<MenuCategory[]>([])
  
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    price: '',
    category: '',
    category_id: null as string | null,
    stock: '',
    image_url: '',
    available: true
  })

  const [newCategoryName, setNewCategoryName] = useState('')
  const [newParentCategoryId, setNewParentCategoryId] = useState<string | null>(null)

  useEffect(() => {
    loadMenu()
  }, [])

  useEffect(() => {
    filterItems()
  }, [searchTerm, selectedParentCategory, selectedSubCategory, menuItems])

  async function loadMenu() {
    setLoading(true)
    try {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) {
        router.push('/login')
        return
      }

      // Get selected restaurant from localStorage
      const selectedRestaurantId = localStorage.getItem('selected_restaurant_id')
      if (!selectedRestaurantId) {
        router.push('/dashboard')
        return
      }

      const { data: restaurants } = await supabase
        .from('restaurants')
        .select('*')
        .eq('id', selectedRestaurantId)
        .eq('owner_id', user.id)
        .single()

      if (!restaurants) return
      setRestaurant(restaurants)

      // Fetch categories
      await fetchCategories(restaurants.id)

      // Fetch menu items
      const { data: items } = await supabase
        .from('menu_items')
        .select('*')
        .eq('restaurant_id', restaurants.id)
        .order('name')

      if (items) {
        // Enrich with category info
        const enrichedItems = items.map((item: any) => {
          if (item.category_id) {
            const categoryInfo = allCategories.find(c => c.id === item.category_id)
            if (categoryInfo) {
              const parent = categoryInfo.parent_id 
                ? allCategories.find(c => c.id === categoryInfo.parent_id)
                : null
              
              return {
                ...item,
                menu_category: {
                  ...categoryInfo,
                  parent: parent || undefined
                }
              }
            }
          }
          return item
        })
        
        setMenuItems(enrichedItems)
      }
    } catch (error) {
      console.error('Error loading menu:', error)
    } finally {
      setLoading(false)
    }
  }

  async function fetchCategories(restaurantId: string) {
    try {
      const { data, error } = await supabase
        .from('menu_categories')
        .select('*')
        .eq('restaurant_id', restaurantId)
        .order('name')

      if (error) {
        console.error('Error fetching categories:', error)
        setAllCategories([])
        setParentCategories([])
        return
      }
      
      const cats = (data || []) as MenuCategory[]
      setAllCategories(cats)
      
      // Filter parent categories
      const parents = cats.filter(c => !c.parent_id)
      setParentCategories(parents)
    } catch (error) {
      console.error('Error fetching categories:', error)
    }
  }

  function filterItems() {
    let filtered = menuItems

    // Filter by parent category
    if (selectedParentCategory !== 'all') {
      filtered = filtered.filter(item => {
        if (item.menu_category) {
          // Check if item's category or its parent matches
          return item.menu_category.id === selectedParentCategory ||
                 item.menu_category.parent_id === selectedParentCategory
        }
        return false
      })
    }

    // Filter by subcategory
    if (selectedSubCategory !== 'all') {
      filtered = filtered.filter(item => item.category_id === selectedSubCategory)
    }

    // Search filter
    if (searchTerm) {
      filtered = filtered.filter(item =>
        item.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        item.description?.toLowerCase().includes(searchTerm.toLowerCase())
      )
    }

    setFilteredItems(filtered)
  }

  function getSubcategories(parentId: string): MenuCategory[] {
    return allCategories.filter(c => c.parent_id === parentId)
  }

  function getCategoryDisplayName(category?: MenuCategory): string {
    if (!category) return ''
    if (category.parent) {
      return `${category.name} (${category.parent.name})`
    }
    return category.name
  }

  function openModal(item: MenuItem | null = null) {
    if (item) {
      setEditingItem(item)
      setFormData({
        name: item.name || '',
        description: item.description || '',
        price: item.price?.toString() || '',
        category: item.category || '',
        category_id: item.category_id || null,
        stock: item.stock?.toString() || '',
        image_url: item.image_url || '',
        available: item.available ?? true
      })
    } else {
      setEditingItem(null)
      setFormData({
        name: '',
        description: '',
        price: '',
        category: '',
        category_id: null,
        stock: '',
        image_url: '',
        available: true
      })
    }
    setShowModal(true)
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault()
    
    if (!restaurant) return

    // Get the selected category to update the category name field
    let categoryName = formData.category
    if (formData.category_id) {
      const selectedCat = allCategories.find(c => c.id === formData.category_id)
      if (selectedCat) {
        categoryName = selectedCat.parent_id
          ? `${selectedCat.name} (${allCategories.find(p => p.id === selectedCat.parent_id)?.name})`
          : selectedCat.name
      }
    }

    const itemData = {
      name: formData.name,
      description: formData.description,
      price: parseFloat(formData.price),
      category: categoryName,
      category_id: formData.category_id,
      stock: parseInt(formData.stock) || 0,
      image_url: formData.image_url,
      available: formData.available,
      restaurant_id: restaurant.id
    }

    try {
      if (editingItem) {
        await supabase
          .from('menu_items')
          .update(itemData)
          .eq('id', editingItem.id)
      } else {
        await supabase
          .from('menu_items')
          .insert(itemData)
      }

      setShowModal(false)
      loadMenu()
    } catch (error) {
      console.error('Error saving menu item:', error)
    }
  }

  async function handleCreateCategory(e: React.FormEvent) {
    e.preventDefault()
    
    if (!restaurant || !newCategoryName.trim()) return

    try {
      const { error } = await supabase
        .from('menu_categories')
        .insert({
          name: newCategoryName.trim(),
          parent_id: newParentCategoryId,
          restaurant_id: restaurant.id
        })

      if (error) throw error

      setShowCategoryModal(false)
      setNewCategoryName('')
      setNewParentCategoryId(null)
      await fetchCategories(restaurant.id)
    } catch (error) {
      console.error('Error creating category:', error)
      alert('Gagal membuat kategori')
    }
  }

  async function deleteItem(id: string) {
    if (!confirm('Hapus item ini dari menu?')) return

    try {
      await supabase
        .from('menu_items')
        .delete()
        .eq('id', id)

      loadMenu()
    } catch (error) {
      console.error('Error deleting item:', error)
    }
  }

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount)
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#FF5A5F]"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white border-b border-gray-200 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button
                onClick={() => router.back()}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <ArrowLeft className="w-5 h-5" />
              </button>
              <h1 className="text-2xl font-bold text-gray-900">Kelola Menu</h1>
            </div>
            <button
              onClick={() => openModal()}
              className="flex items-center gap-2 px-4 py-2 bg-[#FF5A5F] text-white rounded-lg font-medium hover:bg-[#FF5A5F]/90 transition-colors"
            >
              <Plus className="w-5 h-5" />
              Tambah Item
            </button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Search and Filter */}
        <div className="bg-white rounded-2xl p-4 mb-6 border border-gray-100 shadow-sm">
          <div className="flex flex-col md:flex-row gap-4">
            <div className="flex-1 relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
              <input
                type="text"
                placeholder="Cari menu..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
              />
            </div>
            <select
              value={selectedCategory}
              onChange={(e) => setSelectedCategory(e.target.value)}
              className="px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
            >
              <option value="all">Semua Kategori</option>
              {categories.map(cat => (
                <option key={cat} value={cat}>{cat}</option>
              ))}
            </select>
          </div>
        </div>

        {/* Menu Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredItems.length === 0 ? (
            <div className="col-span-full bg-white rounded-2xl p-12 text-center border border-gray-100">
              <p className="text-gray-500 text-lg">Tidak ada item ditemukan</p>
            </div>
          ) : (
            filteredItems.map((item) => (
              <div key={item.id} className="bg-white rounded-2xl overflow-hidden border border-gray-100 shadow-sm hover:shadow-md transition-shadow">
                <div className="aspect-video bg-gray-100 flex items-center justify-center">
                  {item.image_url ? (
                    <img src={item.image_url} alt={item.name} className="w-full h-full object-cover" />
                  ) : (
                    <span className="text-gray-400 text-4xl">üçΩÔ∏è</span>
                  )}
                </div>
                <div className="p-6">
                  <div className="flex items-start justify-between mb-2">
                    <div className="flex-1">
                      <h3 className="font-bold text-lg text-gray-900 mb-1">{item.name}</h3>
                      <span className="inline-block px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded-full">
                        {item.category}
                      </span>
                    </div>
                  </div>
                  <p className="text-sm text-gray-600 mb-4 line-clamp-2">
                    {item.description || 'Tidak ada deskripsi'}
                  </p>
                  <div className="flex items-center justify-between mb-4">
                    <p className="text-2xl font-bold text-gray-900">
                      {formatCurrency(item.price)}
                    </p>
                    <span className={`text-sm font-medium ${item.stock > 10 ? 'text-green-600' : item.stock > 0 ? 'text-yellow-600' : 'text-red-600'}`}>
                      Stok: {item.stock}
                    </span>
                  </div>
                  <div className="flex gap-2">
                    <button
                      onClick={() => openModal(item)}
                      className="flex-1 flex items-center justify-center gap-2 px-4 py-2 bg-blue-500 text-white rounded-lg font-medium hover:bg-blue-600 transition-colors"
                    >
                      <Edit2 className="w-4 h-4" />
                      Edit
                    </button>
                    <button
                      onClick={() => deleteItem(item.id)}
                      className="px-4 py-2 bg-red-500 text-white rounded-lg font-medium hover:bg-red-600 transition-colors"
                    >
                      <Trash2 className="w-4 h-4" />
                    </button>
                  </div>
                </div>
              </div>
            ))
          )}
        </div>
      </main>

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-2xl p-6 max-w-md w-full max-h-[90vh] overflow-y-auto">
            <h2 className="text-2xl font-bold mb-6">
              {editingItem ? 'Edit Item' : 'Tambah Item'}
            </h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Nama Item
                </label>
                <input
                  type="text"
                  required
                  value={formData.name}
                  onChange={(e) => setFormData({...formData, name: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Deskripsi
                </label>
                <textarea
                  value={formData.description}
                  onChange={(e) => setFormData({...formData, description: e.target.value})}
                  rows={3}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Harga
                </label>
                <input
                  type="number"
                  required
                  value={formData.price}
                  onChange={(e) => setFormData({...formData, price: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Kategori
                </label>
                <select
                  required
                  value={formData.category}
                  onChange={(e) => setFormData({...formData, category: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                >
                  <option value="">Pilih Kategori</option>
                  {categories.map(cat => (
                    <option key={cat} value={cat}>{cat}</option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Stok
                </label>
                <input
                  type="number"
                  required
                  value={formData.stock}
                  onChange={(e) => setFormData({...formData, stock: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  URL Gambar
                </label>
                <input
                  type="url"
                  value={formData.image_url}
                  onChange={(e) => setFormData({...formData, image_url: e.target.value})}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#FF5A5F]"
                />
              </div>
              <div className="flex gap-2 pt-4">
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  className="flex-1 px-4 py-2 border border-gray-200 text-gray-700 rounded-lg font-medium hover:bg-gray-50 transition-colors"
                >
                  Batal
                </button>
                <button
                  type="submit"
                  className="flex-1 px-4 py-2 bg-[#FF5A5F] text-white rounded-lg font-medium hover:bg-[#FF5A5F]/90 transition-colors"
                >
                  {editingItem ? 'Simpan' : 'Tambah'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  )
}
